body {
	padding-top: 110px;
	@include media-breakpoint-down(md) {
		padding-top: 80px; } }
header {
	background: $blue;
	@include pos(fix, 0 0 null 0);
	z-index: 1000;
	&.minimal {
		box-shadow: 0px 10px 20px -10px color-rgba($black,0.1); }
	.header-top {
		background: $blue; }
	.page-nav {
		@include pos(rel,null);
		.toggle-pagenav {
			display: none;
			@include media-breakpoint-down(md) {
				display: block;
				line-height: 30px;
				background: color-rgba($white,0.1);
				color: $white;
				text-transform: uppercase;
				font-weight: bold;
				@include fs(13rem);
				padding: 0px 15px;
				min-width: 150px;
				@include pos(rel,null);
				&:after {
					content: '\f107';
					@include pos(abs, 0px 10px null null); } } }
		ul {
			@include flexbox;
			align-items: center;
			align-content: center;
			@include media-breakpoint-down(md) {
				@include pos(abs, 100% null null 0%);
				display: none;
				background: darken($blue,5%);
				min-width: 100%;
				padding: 5px 0; }
			li {
				+ li {
					margin-left: 35px;
					@include media-breakpoint-down(lg) {
						margin-left: 20px; }
					@include media-breakpoint-down(md) {
						margin-left: 0; } }
				a {
					display: block;
					@include fs(14rem);
					color: $white;
					text-transform: uppercase;
					font-weight: bold;
					line-height: 20px;
					padding: 10px 0;
					@include pos(rel,null);
					@include media-breakpoint-down(lg) {
						@include fs(13rem); }
					@include media-breakpoint-down(md) {
						padding: 5px 15px; }
					&:after {
						content: '';
						@include pos(abs, null null 0 50%);
						@include transform(translateX(-50%));
						z-index: 1;
						border: 5px solid transparent;
						border-top: none;
						border-bottom-color: $white;
						opacity: 0;
						@include transition(all,0.5s);
						@include media-breakpoint-down(md) {
							display: none; } } }
				&:hover {
					a {
						color: $yellow; } }
				&.active {
					a {
						color: $yellow;
						@include media-breakpoint-down(md) {
							background: color-rgba($white,0.2); }
						&:after {
							opacity: 1; } } } } } }

	.header-wrap {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: space-between; }
	.col-right, .header-tool {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: flex-end; }
	.branch {
		a {
			@include fs(14rem);
			font-weight: bold;
			color: $white;
			@include media-breakpoint-down(lg) {
				@include fs(13rem); }
			.fa {
				@include fs(16rem);
				vertical-align: middle;
				margin-right: 10px; }
			&:hover {
				color: $yellow; } } }
	.hotline {
		margin-left: 30px;
		@include media-breakpoint-down(md) {
			margin-left: 15px; }
		a {
			@include fs(14rem);
			font-weight: bold;
			color: $white;
			@include media-breakpoint-down(lg) {
				@include fs(13rem); }
			.fa {
				@include fs(16rem);
				vertical-align: middle;
				margin-right: 10px; }
			&:hover {
				color: $yellow; } } }
	.search {
		margin-left: 30px;
		@include media-breakpoint-down(md) {
			margin: 0;
			@include pos(rel,null); }
		.search-toggle {
			width: 30px;
			height: 30px;
			@include bor-rad(50px);
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			color: $blue;
			border: 1px solid darken($white,5%);
			.material-icons {
				@include fs(18rem); } }
		.search-wrap {
			@include media-breakpoint-down(md) {
				@include pos(abs, calc(100% + 10px) 0 null null);
				display: none; } }
		.searchbox {
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: space-between;
			height: 25px;
			@include bor-rad(50px);
			background: darken($white,5%);
			padding: 0px 10px;
			width: 115px;
			@include media-breakpoint-down(md) {
				width: 200px;
				background: $blue;
				@include bor-rad(5px);
				box-shadow: 0px 3px 20px color-rgba($black,0.1);
				padding: 5px 10px;
				height: auto; }
			input {
				width: calc(100% - 20px);
				border: none;
				@include fs(14rem);
				color: darken($white,40%);
				background: none;
				@include media-breakpoint-down(md) {
					color: $white;
					@include fs(15rem);
					line-height: 20px; } }
			.searchbutton {
				@include fs(14rem);
				color: darken($white,40%);
				@include media-breakpoint-down(md) {
					color: $white;
					@include fs(16rem); }
				&:hover {
					color: $main; } }
			&:hover {
				@include media-breakpoint-up(lg) {
					background: $white; } } } }
	.language {
		margin-left: 10px;
		a {
			display: block;
			@include fs(12rem);
			color: $blue;
			line-height: 25px;
			padding: 0px 20px;
			@include bor-rad(50px);
			background: darken($white,5%);
			@include media-breakpoint-down(md) {
				line-height: 22px;
				padding: 0px 15px; }
			&:hover {
				background: $white; } } }
	.header-main {
		background: $white; }
	.sitelogo {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		height: 70px;
		@include media-breakpoint-down(md) {
			height: 50px; }
		img {
			@include media-breakpoint-down(md) {
				height: 40px; } } }
	.btn-showmenu {
		display: none;
		@include media-breakpoint-down(md) {
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			width: 30px;
			height: 30px;
			@include bor-rad(5px);
			background: $blue;
			@include fs(18rem);
			color: $white; } }
	.overlay {
		display: none;
		@include pos(fix, 0 null 0 0);
		z-index: 800;
		right: 0;
		background: $black;
		opacity: 0.5; }
	.menu {
		@include flexbox;
		align-items: center;
		align-content: center;
		@include media-breakpoint-down(md) {
			display: block;
			@include pos(fix, 0 null 0 0);
			z-index: 1000;
			width: 280px;
			background: $white;
			@include transition(all,0.5s);
			@include transform(translateX(-100%));
			&.open {
				@include transform(translateX(0)); } }
		.btn-closemenu {
			display: none;
			@include media-breakpoint-down(md) {
				@include pos(abs, 10px 10px null null);
				width: 20px;
				height: 20px;
				@include bor-rad(50px);
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				background: darken($white,5%);
				@include bor-rad(50px);
				opacity: 0.5;
				background-color: $red;
				color: $white; }
			.material-icons {
				line-height: 1;
				@include fs(14rem); } }
		.menu-logo {
			display: none;
			@include media-breakpoint-down(md) {
				display: block; }
			.sitelogo {
				padding: 25px 0;
				height: auto;
				border-bottom: 3px solid $blue; } }
		.menu-wrap {}
		ul.menu-link {
			@include flexbox;
			@include media-breakpoint-down(md) {
				display: block; }
			> li {
				@include media-breakpoint-down(md) {
					border-bottom: 1px solid darken($white,10%); }
				> a {
					display: block;
					@include fs(14rem);
					line-height: 20px;
					font-weight: bold;
					text-transform: uppercase;
					color: $main;
					padding: 25px 30px;
					@include media-breakpoint-down(md) {
						padding: 10px 20px; } }
				&.has-sub {
					@include pos(rel,null);
					> a {
						@include pos(rel,null);
						&:after {
							content: '\f0d7';
							@include pos(abs, 25px 10px null null);
							@include media-breakpoint-down(md) {
								display: none; } } }
					.btn-showsub {
						display: none;
						@include media-breakpoint-down(md) {
							@include flexbox;
							width: 40px;
							height: 40px;
							@include pos(abs, 0 0 null null);
							z-index: 3;
							justify-content: center;
							align-items: center;
							align-content: center;
							@include fs(18rem);
							&:before {
								content: '\f107'; }
							&.active {
								@include transform(scale(1,-1)); } } }
					.sub {
						@include pos(abs, 90% null null 15px);
						width: 200px;
						@include bor-rad(5px);
						background: $white;
						z-index: 1;
						border: 1px solid darken($white,5%);
						box-shadow: 0px 3px 20px color-rgba($black,0.1);
						padding: 5px 0;
						display: none;
						@include media-breakpoint-down(md) {
							@include pos(static,null);
							padding: 0px;
							box-shadow: none;
							border: none;
							background: darken($white,5%);
							width: 100%;
							border-top: 1px solid darken($white,10%);
							@include bor-rad(0); }
						ul {
							li {
								a {
									color: darken($white,60%);
									display: block;
									padding: 8px 15px;
									@include fs(13rem);
									@include media-breakpoint-down(md) {
										padding: 8px 20px; } }
								&:hover {
									a {
										@include media-breakpoint-up(xl) {
											background: $blue;
											color: $white; } } }
								&.active {
									a {
										@include media-breakpoint-down(md) {
											background: $blue;
											color: $white; } } } } } } }
				&:hover {
					> a {
						color: $blue; }
					@include media-breakpoint-up(lg) {
						.sub {
							display: block; } } }
				&.active {
					> a {
						color: $blue;
						@include media-breakpoint-down(md) {
							background: $blue;
							color: $white; } } } } } }
	.ebanking {
		margin-left: 30px;
		@include media-breakpoint-down(md) {
			margin-left: 0; }
		a {
			display: block;
			@include fs(12rem);
			text-transform: uppercase;
			font-weight: bold;
			color: $white;
			background: $red;
			line-height: 36px;
			padding: 0px 20px;
			border: 2px solid darken($white,10%);
			@include bor-rad(50px);
			@include media-breakpoint-down(md) {
				@include bor-rad(0);
				border: none;
				line-height: 40px; }
			.fa {
				@include fs(16rem);
				margin-right: 5px;
				vertical-align: middle; }
			&:hover {
				background: $yellow;
				color: $main; } } } }
