// FORM COMPONENTS

.checkbox {
	input {
		display: none;
		+ label {
			color: darken($white,60%);
			padding: 0 0 0 25px;
			@include pos(rel,null);
			&:hover {
				color: $orange; }
			&:before {
				content: '';
				display: block;
				width: 16px;
				height: 16px;
				@include bor-rad(3px);
				border: 1px solid darken($white,10%);
				@include pos(abs, 3px null null 0); }
			&:after {
				content: '\f00c';
				font-family: 'fontawesome';
				@include flexbox;
				justify-content: center;
				align-items: center;
				align-content: center;
				width: 16px;
				height: 16px;
				@include fs(12rem);
				@include bor-rad(3px);
				color: $white;
				background: $green;
				border-color: $green;
				@include pos(abs, 3px null null 0);
				z-index: 1;
				@include transition(all,0.5s);
				opacity: 0; } }
		&:checked {
			+ label {
				color: $main;
				&:after {
					opacity: 1; } } } } }
.radio {
	input[type="radio"] {
		display: none;
		+ label {
			padding: 0 0 0 25px;
			@include pos(rel,null);
			@include transition(all,0.3s);
			&:hover {
				color: $orange; }
			&:before {
				content: '';
				display: block;
				width: 16px;
				height: 16px;
				@include bor-rad(50px);
				@include pos(abs, 4px null null 0);
				border: 2px solid darken($white,60%); }
			&:after {
				content: '';
				display: block;
				width: 12px;
				height: 12px;
				background: darken($white,60%);
				border: 3px solid $white;
				@include pos(abs, 6px null null 2px);
				@include bor-rad(50px);
				opacity: 0;
				@include transition(all,0.3s); } }
		&:checked {
			+ label {
				&:after {
					opacity: 1; } } } } }
.file {
	input[type="file"] {
		display: none; }
	label {
		&:hover {
			color: $black; } } }
