body.modal-open {
	padding-right: 0 !important;
	overflow: hidden;
	.modal {
		&::-webkit-scrollbar-track {}
		&::-webkit-scrollbar {
			width: 0; }
		&::-webkit-scrollbar-thumb {} } }
.modal-backdrop {
 }	// background: $white
.modal {
	padding-right: 0 !important;
	.modal-dialog {
		margin: 100px auto;
		max-width: 1000px;
		.modal-content {
			@include bor-rad(0);
			border: none;
			box-shadow: none; }
		.modal-dialog {}
		margin: 100px auto;
		max-width: 1000px;
		@include media-breakpoint-down(md) {
			margin: 60px auto; }
		@include media-breakpoint-down(xs) {
			margin: 0px; }
		.modal-content {
			@include bor-rad(0);
			border: none;
			box-shadow: none; }
		&.modal-order {
			background: $main;
			max-width: 650px;
			@include pos(rel,null);
			@include media-breakpoint-down(xs) {
				padding: 0px 30px;
				max-width: 100%;
				width: 100%; }
			.modal-content {
				background: none;
				padding: 0;
				@include pos(static,null);
				.modal-header {
					border: none;
					padding: 0;
					margin: 0;
					.close {
						color: $white;
						text-shadow: none;
						opacity: 1;
						line-height: 1;
						@include pos(abs, 15px 15px null null);
						z-index: 10;
						padding: 0;
						margin: 0;
						.lnr {
							line-height: 1; }
						@include media-breakpoint-down(xs) {
							top: 15px;
							right: 15px; } } }
				.modal-body {
					padding: 40px 0; } }
			.order-form {
				max-width: 500px;
				margin: 0 auto;
				.module-title {
					color: $white;
					@include fs(16rem);
					margin-bottom: 10px; }
				.form-wrap {
					.form-group {
						label {
							display: block; } } }
				.frm-btn {
					input[type="submit"] {
						// background: $white
						// color: $red
						font-weight: 500; } } }
			.inner-content {
				@include media-breakpoint-down(sm) {
					padding: 40px 0px !important; } }
			.thankyou-content {
				img {
					height: 100px;
					margin-bottom: 20px; }
				h3 {
					font-size: 1.5rem !important; }
				h4 {
					font-size: 0.875rem !important; } } } } }
