// REGULAR COMMON

.ct-captcha {
	width: auto;
	@include flexbox;
	align-items: center;
	align-content: center;
	label {
		display: none; } }
.RadCaptcha {
	margin: 0;
	@include flexbox;
	flex-wrap: wrap;
	@include pos(rel,null);
	span {
		@include fs(14rem);
		color: $red;
		order: 2;
		position: absolute;
		top: 15px;
		right: 15px;
		@include fs(0);
		&:before {
			content: '\f071';
			@include fs(16rem);
			color: red; } }
	> div {
		order: 1;
		@include flexbox;
		align-items: center;
		align-content: center;
		width: 100%;
		> div {
			@include flexbox;
			align-items: center;
			align-content: center;
			a {
				@include fs(0);
				margin-left: 20px;
				&:before {
					content: '\f021';
					@include fs(18rem); }
				&:hover {
					color: $red; } } }
		> p {
			display: block;
			margin-left: 20px;
			input {} } } }


