.subscribe {
	.alert-success {
		font-size: 0.875rem;
		line-height: 1.275rem;
		font-style: italic;
		display: block;
		width: 100%;
		text-align: center;
		color: $red;
		@include fs(18rem);
		border: none;
		background-color: rgba(255, 255, 255, 0.8);
		padding: 10px;
		margin: 0;
		border-radius: 0; } }
.subscribefrm {
	position: relative;
	.fa.fa-exclamation-triangle {
		color: red;
		@include fs(15rem);
		display: block;
		position: absolute;
		top: 10px;
		right: 15px;
		text-indent: -9999px;
		width: 15px;
		height: 15px;
		line-height: 15px;
		text-align: center;
		&:after {
			content: '\f071';
			font-size: 12px;
			color: red;
			text-indent: 0;
			float: left;
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0; }
		@media only screen and (min-width: 1024px) {
			right: 15px;
			top: 10px; } }
	.fa-exclamation-triangle:before, .fa-warning:before {
		margin-right: 5px; } }
.form-group {
	position: relative;
	.fa.fa-exclamation-triangle {
		position: absolute;
		top: 12px;
		right: 15px;
		@include fs(0);
		&:before {
			content: '\f071';
			@include fs(16rem);
			color: red; } } }
