.register-page {
	padding: 50px 0;
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.register-link {
		text-align: center;
		a {
			@include fs(15rem);
			color: $blue;
			padding: 0px 10px;
			@include pos(rel,null);
			display: inline-block;
			+ a {
				&:before {
					content: '';
					display: block;
					width: 1px;
					background: darken($white,20%);
					@include pos(abs, 2px null 2px 0); } }
			&:hover {
				color: $red;
				text-decoration: underline; } } } }

// register form
.register-form {
	padding-top: 30px;
	.module-title {
		margin-bottom: 30px;
		text-align: center;
		@include pos(rel,null);
		&:after {
			content: '';
			display: block;
			height: 1px;
			@include pos(abs, 50% 0 null 0);
			background: darken($white,10%); }
		span {
			display: inline-block;
			@include fs(24rem);
			text-transform: uppercase;
			color: darken($white,60%);
			background: $white;
			@include pos(rel,null);
			z-index: 1;
			padding: 0px 10px; } }
	.card-list {
		.cre-card {
			text-align: center;
			margin-bottom: 30px;
			input {
				display: none;
				+ label {
					@include pos(rel,null);
					padding-bottom: 30px;
					&:before {
						content: '';
						width: 18px;
						height: 18px;
						@include bor-rad(5px);
						border: 1px solid darken($white,20%);
						@include pos(abs, null null 0 50%);
						@include transform(translateX(-50%)); }
					&:after {
						content: '';
						width: 10px;
						height: 10px;
						@include bor-rad(3px);
						background: $red;
						@include pos(abs, null null 4px 50%);
						@include transform(translateX(-50%));
						z-index: 1;
						opacity: 0; }
					.name {
						text-transform: uppercase;
						@include fs(24rem);
						font-weight: bold;
						letter-spacing: 10px; } }
				&:checked {
					+ label {
						&:before {
							border-color: darken($white,40%); }
						&:after {
							opacity: 1; } } } } } }
	.register-step {
		@include flexbox;
		.step {
			width: 25%;
			background: darken($white,10%);
			@include fs(15rem);
			font-weight: bold;
			text-transform: uppercase;
			color: darken($white,40%);
			line-height: 20px;
			padding: 15px 0;
			text-align: center;
			&.active {
				background: $blue;
				color: $white; } } }
	.form-wrap {
		padding: 30px 0;
		max-width: 920px;
		margin: 0 auto; }
	.form-note {
		@include fs(14rem);
		color: darken($white,40%); }
	.form-group {
		margin-bottom: 10px; } }
