#scrollUp {
	width: 50px;
	height: 50px;
	@include flexbox;
	align-items: center;
	align-content: center;
	justify-content: center;
	color: $blue;
	background: $white;
	@include pos(fix, null 25px 0 null);
	z-index: 100;
	@include fs(0);
	@include media-breakpoint-down(sm) {
		width: 36px;
		height: 36px; }
	&:before {
		content: '\f106';
		@include fs(28rem);
		font-weight: bold;
		@include media-breakpoint-down(sm) {
			@include fs(24rem); } } }
