.product-section {
	background: $white;
	padding: 50px 0;
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.product-list {
		.product {
			text-align: center;
			margin-bottom: 60px;
			.product-img {
				display: block;
				img {} }
			.product-btn {} } } }
.product-page {}
.product-detail {
	padding: 50px 0;
	@include pos(rel,null);
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.product-bg {
		@include pos(abs, 0 0 null 0);
		@include media-breakpoint-down(md) {
			bottom: 0; }
		img {
			width: 100%;
			@include media-breakpoint-down(sm) {
				height: 100%;
				object-fit: cover; } } }
	.container {
		@include pos(rel,null);
		z-index: 1; }
	.product-cate {
		margin: 0px auto 5px; }
	.product-name {
		@include fs(24rem);
		text-transform: uppercase;
		color: $main;
		margin-bottom: 60px; }
	.product-img {
		margin-bottom: 70px; } }
.product-title {
	@include fs(24rem);
	font-weight: bold;
	margin-bottom: 25px;
	text-transform: uppercase;
	@include media-breakpoint-down(sm) {
		@include fs(20rem);
		margin-bottom: 20px; } }
.product-feature {
	padding: 50px 0;
	background: $white;
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.feature-content {
		padding: 30px 0;
		.title {
			@include fs(24rem);
			font-weight: bold;
			margin-bottom: 25px;
			text-transform: uppercase; }
		.content {
			@include fs(14rem); }
		.feature {
			@include flexbox;
			align-items: center;
			align-content: center;
			margin-bottom: 25px;
			.icon {
				@include flexbox;
				align-items: center;
				align-content: center;
				width: 40px; }
			.name {
				width: calc(100% - 40px);
				@include fs(14rem);
				text-transform: uppercase;
				color: darken($white,60%); } }
		.btn-viewmore {
			@include fs(12rem);
			font-weight: bold;
			color: $white;
			background: $blue;
			padding: 0px 30px;
			@include bor-rad(50px);
			display: inline-block;
			line-height: 36px;
			text-transform: uppercase;
			&:hover {
				background: $yellow;
				color: $main; } } } }
.product-limit {
	background: darken($white,4%);
	@include pos(rel,null);
	.product-img {
		text-align: right; }
	.limit-content {
		@include pos(abs,0 null 0 auto);
		width: 540px;
		@include flexbox;
		flex-direction: column;
		justify-content: center;
		background: linear-gradient(to right, darken($white,4%) 50%,color-rgba(darken($white,4%),0));
		@include media-breakpoint-down(sm) {
			@include pos(static,null);
			width: 100%;
			padding: 30px 0; }
		.title {}
		.content {
			@include fs(14rem); } } }
.product-promotion {
	padding: 50px 0;
	background: $white;
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.promotion-list {
		.promotion {
			margin-bottom: 20px;
			figure {
				.promotion-img {
					img {
						width: 100%; } }
				figcaption {
					text-align: center;
					padding: 20px 10px;
					.promotion-name {
						@include fs(24rem);
						color: $blue;
						margin-bottom: 10px; }
					.promotion-des {
						@include fs(15rem); } } } } } }
.product-register {
	background: $blue;
	@include media-breakpoint-down(md) {
		padding: 20px 0; }
	.register-wrap {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: space-between;
		@include media-breakpoint-down(md) {
			flex-direction: column;
			justify-content: center; }
		.register-intro {
			@include flexbox;
			align-items: center;
			align-content: center;
			@include media-breakpoint-down(md) {
				flex-direction: column;
				align-items: center;
				align-content: center;
				text-align: center; }
			.module-title {
				@include fs(18rem);
				text-transform: uppercase;
				color: $white;
				@include media-breakpoint-down(lg) {
					@include fs(16rem); } }
			.btn-register {
				@include fs(12rem);
				text-transform: uppercase;
				font-weight: bold;
				color: $white;
				background: $red;
				@include bor-rad(50px);
				padding: 0px 35px;
				line-height: 34px;
				margin-left: 30px;
				@include media-breakpoint-down(lg) {
					margin-left: 10px; }
				@include media-breakpoint-down(md) {
					margin: 10px 0; } } }
		.register-pr {
			padding: 10px 0;
			text-align: right;
			margin-top: -80px;
			&.s2 {
				margin-top: -38px; }
			@include media-breakpoint-down(md) {
				margin-top: 0; }
			img {} } } }
