.breadcrumb-wrap {
	.breadcrumb-content {
		background: darken($white,10%); }
	.breadcrumb {
		margin: 0;
		padding: 10px 0;
		background: none;
		@include bor-rad(0);
		@include flexbox;
		li {
			&:after {
				content: '\f105';
				margin: 0px 10px; }
			a {
				@include fs(13rem);
				line-height: 20px;
				color: darken($white,60%); }
			&:last-child {
				&:after {
					display: none; }
				a {
					color: $blue; } } } }
	.page-banner {
		@include pos(rel,null);
		img {
			width: 100%;
			@include media-breakpoint-down(lg) {
				height: 300px;
				object-fit: cover; }
			@include media-breakpoint-down(md) {
				height: 250px; }
			@include media-breakpoint-down(sm) {
				height: 200px; } }
		.banner-caption {
			@include pos(abs, 20% null null auto);
			max-width: 450px;
			@include media-breakpoint-down(xs) {
				right: 15px;
				left: 15px; }
			.big {
				@include fs(40rem);
				line-height: 1.3;
				font-weight: bold;
				color: $blue;
				margin-bottom: 10px;
				@include media-breakpoint-down(md) {
					@include fs(32rem);
					margin-bottom: 5px; } }
			.des {
				@include fs(15rem);
				color: $main;
				@include media-breakpoint-down(md) {
					@include fs(14rem); } } } } }
